@import '../mixins';

.split-accordion {
    padding: 120px 125px;

    &__accordion {
        &__paragraph {
            color: var(--Wivians-Factory-Gray, #3F3F3E);

            /* Desktop/Small Body */
            font-family: Inter Tight;
            font-size: 16px;
            font-style: normal;
            font-weight: 400;
            line-height: 24.2px;
            /* 151.25% */
            letter-spacing: 0.32px;
        }

        .accordion {

            .accordion-item {
                border: 0;
                border-bottom: 1px solid #AAAAAA;
                border-radius: 0;

                &,
                .accordion-button {
                    background-color: transparent;
                    color: var(--Wivians-Factory-Gray, #3F3F3E);

                    /* Desktop/H3 */
                    font-family: Inter Tight;
                    font-size: 20px;
                    font-style: normal;
                    font-weight: 700;
                    line-height: normal;
                    letter-spacing: 0.4px;

                    &:focus {
                        border-radius: 0;
                        box-shadow: none;
                    }

                    &::after {
                        display: none;
                    }


                }

                .accordion-button {
                    overflow: hidden;
                    display: flex;
                    align-items: center;
                    gap: 0 8px;
                    transition: all .5s ease;

                    &::before {
                        content: url('../assets/Line.svg');
                        opacity: 0;
                        visibility: hidden;
                        width: 0;
                        height: 37px;
                        transition: all .3s ease-out;
                    }

                    &:hover {
                        transform: translateX(10px);

                        &::before {
                            visibility: visible;
                            opacity: 1;
                            width: auto;
                            transition: all .3s ease-out;
                        }

                    }

                }

                .accordion-body {
                    padding:0 !important;
                }

                .accordion-button {
                    padding: 16px 0 !important;
                }

                .accordion-button:not(.collapsed) {
                    box-shadow: none;
                }

            }
        }
    }

}